import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import classnames from 'classnames'
import Card from '@material-ui/core/Card'
import Grid from '@material-ui/core/Grid'
import CardHeader from '@material-ui/core/CardHeader'
import CardMedia from '@material-ui/core/CardMedia'
import CardContent from '@material-ui/core/CardContent'
import CardActions from '@material-ui/core/CardActions'
import Collapse from '@material-ui/core/Collapse'
import Avatar from '@material-ui/core/Avatar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import FavoriteIcon from '@material-ui/icons/Favorite'
import ShareIcon from '@material-ui/icons/Share'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import { getImageMovie } from '../../common/utils/helpers'
import Link from '../../components/Link'
import moment from 'moment'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'

const styles = theme => ({
  card: {
    maxWidth: 400
  },
  media: {
    height: 0,
    paddingTop: '56.25%' // 16:9
  },
  actions: {
    display: 'flex',
    justifyContent: 'space-between'
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: 'rotate(180deg)'
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main
  },
  root: {
    width: '100%',
    maxWidth: 380,
    backgroundColor: theme.palette.background.paper
  },
  inline: {
    display: 'inline'
  }
})

class MovieCard extends Component {
  state = { expanded: false }

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }))
  }

  render() {
    const { classes, movie } = this.props
    const { uploader = {} } = movie
    return (
      <Card className={classes.card}>
        {/* <CardHeader
          avatar={
            <Avatar aria-label="Recipe" className={classes.avatar}>
              R
            </Avatar>
          }
          action={
            <IconButton>
              <MoreVertIcon />
            </IconButton>
          }
          title="Shrimp and Chorizo Paella"
          subheader="September 14, 2016"
        /> */}
        <CardMedia className={classes.media} image={getImageMovie(movie)} title="Paella dish" />
        <CardContent>
          {/* <Grid
            container
            direction="column"
            // justify="space-evenly"
            alignContent="space-between"
            // alignItems="flex-start"
          >
            <Grid item xs={12}>
              <Typography>
                <Link variant="subtitle1" href={`/movies/${movie._id}`} color="textPrimary">
                  {movie.name}
                </Link>
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography>
                <Link variant="caption" href={`/profile/${uploader._id}`} color="textPrimary">
                  {uploader.name}
                </Link>
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="caption" color="textPrimary">
                Link
              </Typography>
            </Grid>
          </Grid> */}
          <List className={classes.root}>
            <ListItem alignItems="flex-start">
              <ListItemText
                primary="Brunch this weekend?"
                secondary={
                  <React.Fragment>
                    <Typography component="span" className={classes.inline} color="textPrimary">
                      Ali Connors
                    </Typography>
                    {" — I'll be in your neighborhood doing errands this…"}
                  </React.Fragment>
                }
              />
            </ListItem>
            <ListItem alignItems="flex-start">
              <ListItemText
                primary="Summer BBQ"
                secondary={
                  <React.Fragment>
                    <Typography component="span" className={classes.inline} color="textPrimary">
                      to Scott, Alex, Jennifer
                    </Typography>
                    {" — Wish I could come, but I'm out of town this…"}
                  </React.Fragment>
                }
              />
            </ListItem>
            <ListItem alignItems="flex-start">
              <ListItemText
                primary="Oui Oui"
                secondary={
                  <React.Fragment>
                    <Typography component="span" className={classes.inline} color="textPrimary">
                      Sandra Adams
                    </Typography>
                    {' — Do you have Paris recommendations? Have you ever…'}
                  </React.Fragment>
                }
              />
            </ListItem>
          </List>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
          {/* <IconButton aria-label="Add to favorites">
            <FavoriteIcon />
          </IconButton>
          <IconButton aria-label="Share">
            <ShareIcon />
          </IconButton> */}
          <Typography>
            <Typography inline={true} variant="caption" color="textPrimary">
              {movie.viewsCount} views ・
            </Typography>
            <Typography inline={true} variant="caption" color="textPrimary">
              {moment(movie.createdAt).fromNow()}
            </Typography>
          </Typography>
          <IconButton
            // className={classnames(classes.expand, {
            // [classes.expandOpen]: this.state.expanded
            // })}
            // onClick={this.handleExpandClick}
            // aria-expanded={this.state.expanded}
            aria-label="Show more"
          >
            <MoreVertIcon />
          </IconButton>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
          <CardContent />
        </Collapse>
      </Card>
    )
  }
}

MovieCard.propTypes = {
  classes: PropTypes.object.isRequired,
  movie: PropTypes.object.isRequired
}

export default withStyles(styles)(MovieCard)
